import React, { useState } from 'react'
import SingleColor from './SingleColor'

import Values from 'values.js'

function App() {

  const [color, setColor] = useState('');
  const [error, setError] = useState(false);
  let [list, setList] = useState(new Values('#f15025').all(10));

  const handleSubmit = (e) => {
    e.preventDefault();

    try{

      let colors = new Values(color).all(10);
      setList(colors);


    } catch(error){
      setError(true);
      console.log(error);
    }
    
 


    // end handleSubmit
  }

  return (
    <>
    <section className='container'>
    <h3>color generator</h3>

    <form onSubmit={handleSubmit}>
    <input type='text' value={color} onChange={e=> setColor(e.target.value)} placeholder='#f15025' className={error? 'error' : null}/>
    <button className="btn" type='submit'>submit</button>
    </form>
    </section>


    <section className="colors">
      {list.map((value, index)=> {
        console.log(value);
        return (
          <SingleColor key={index} {...value} index={index} hexColor={value.hex}/>
        )
      })}
    </section>

    </>
  )
}

export default App
